async function readData<T>(filePath: string): Promise<T[]> {
  await ensureDataFilesExist();
  try {
    const jsonData = await fs.readFile(filePath, 'utf-8');
    return JSON.parse(jsonData) as T[];
  } catch (error: any) {
    console.error(`读取数据错误 ${filePath}:`, error);
    return []; 
  }
}

async function writeData<T>(filePath: string, data: T[]): Promise<void> {
  try {
    const jsonData = JSON.stringify(data, null, 2);
    await fs.writeFile(filePath, jsonData, 'utf-8');
  } catch (error) {
    console.error(`写入数据错误 ${filePath}:`, error);
    throw error;
  }
}

export async function getEvents(): Promise<Event[]> {
  return readData<Event>(EVENTS_FILE);
}

export async function getEventById(id: string): Promise<Event | undefined> {
  const events = await readData<Event>(EVENTS_FILE);
  return events.find(e => e.id === id);
}

export async function createEvent(eventData: Omit<Event, 'id' | 'participants' | 'organizerId'>, organizerId: string): Promise<Event> {
  const events = await readData<Event>(EVENTS_FILE);
  const newEvent: Event = {
    ...eventData,
    id: `event_${Date.now()}_${Math.random().toString(36).substring(2, 7)}`,
    participants: [],
    organizerId, 
  };
  events.push(newEvent);
  await writeData<Event>(EVENTS_FILE, events);
  return newEvent;
}

export async function joinEvent(userId: string, eventId: string, cdKey: string): Promise<{ success: boolean, message: string, event?: Event }> {
  let events = await readData<Event>(EVENTS_FILE);
  let users = await readData<User>(USERS_FILE);

  const eventIndex = events.findIndex(e => e.id === eventId);
  if (eventIndex === -1) return { success: false, message: '活动未找到。' };
  const event = events[eventIndex];

  const userIndex = users.findIndex(u => u.id === userId); 
  if (userIndex === -1) return { success: false, message: '用户未找到。' };
  const user = users[userIndex];

  if (event.cdKey.toLowerCase() !== cdKey.toLowerCase()) return { success: false, message: '无效的激活码。' };
  if (event.participantLimit && event.participants.length >= event.participantLimit) {
    return { success: false, message: '活动人数已满。' };
  }
  if (event.participants.some(p => p.id === userId)) { 
    return { success: true, message: '您已加入此活动。', event };
  }

  const participant: Participant = {
    id: user.id, 
    username: user.username,
    avatarUrl: user.avatarUrl,
    isCheckedIn: false,
    op: user.op || 0, // Ensure op is carried over
  };
  event.participants.push(participant);

  if (!user.joinedEventIds) {
    user.joinedEventIds = [];
  }
  if (!user.joinedEventIds.includes(eventId)) {
    user.joinedEventIds.push(eventId);
  }

  events[eventIndex] = event;
  users[userIndex] = user;

  await writeData<Event>(EVENTS_FILE, events);
  await writeData<User>(USERS_FILE, users);

  return { success: true, message: '成功加入活动！', event };
}

export async function leaveEvent(userId: string, eventId: string): Promise<{ success: boolean, message: string, event?: Event }> {
  let events = await readData<Event>(EVENTS_FILE);
  let users = await readData<User>(USERS_FILE);

  const eventIndex = events.findIndex(e => e.id === eventId);
  if (eventIndex === -1) return { success: false, message: '活动未找到。' };
  const event = events[eventIndex];

  const userIndex = users.findIndex(u => u.id === userId); 
  if (userIndex === -1) return { success: false, message: '用户未找到。' };
  const user = users[userIndex];

  const participantIndexInEvent = event.participants.findIndex(p => p.id === userId); 
  if (participantIndexInEvent === -1) {
    if (user.joinedEventIds && user.joinedEventIds.includes(eventId)) {
        user.joinedEventIds = user.joinedEventIds.filter(id => id !== eventId);
        users[userIndex] = user;
        await writeData<User>(USERS_FILE, users);
        return { success: true, message: '已从您的活动列表中移除。您并未实际参加此活动。', event };
    }
    return { success: false, message: '您当前未加入此活动。' };
  }

  event.participants.splice(participantIndexInEvent, 1);
  if (user.joinedEventIds) {
    user.joinedEventIds = user.joinedEventIds.filter(id => id !== eventId);
  }

  events[eventIndex] = event;
  users[userIndex] = user;

  await writeData<Event>(EVENTS_FILE, events);
  await writeData<User>(USERS_FILE, users);

  return { success: true, message: '成功离开活动。', event };
}

export async function checkInUser(userId: string, eventId: string): Promise<{ success: boolean, message: string }> {
  let events = await readData<Event>(EVENTS_FILE);
  const eventIndex = events.findIndex(e => e.id === eventId);

  if (eventIndex === -1) return { success: false, message: '活动未找到。' };
  const event = events[eventIndex];

  const participantIndexInEvent = event.participants.findIndex(p => p.id === userId); 
  if (participantIndexInEvent === -1) return { success: false, message: '您未报名参加此活动。' };

  const participant = event.participants[participantIndexInEvent];
  if (participant.isCheckedIn) return { success: true, message: '您已签到。' };

  const eventDate = parseISO(event.date);
  const today = new Date();
  const isCurrent = eventDate.getFullYear() === today.getFullYear() &&
                    eventDate.getMonth() === today.getMonth() &&
                    eventDate.getDate() === today.getDate();

  if (!isCurrent) { 
    return { success: false, message: '此活动的签到尚未开始或已结束。' };
  }

  participant.isCheckedIn = true;
  event.participants[participantIndexInEvent] = participant;
  events[eventIndex] = event;

  await writeData<Event>(EVENTS_FILE, events);
  return { success: true, message: '签到成功！' };
}

export async function getUserById(userId: string): Promise<User | undefined> {
  await ensureDataFilesExist();
  const users = await readData<User>(USERS_FILE);
  return users.find(u => u.id === userId);
}

export async function getUserByUsername(username: string): Promise<User | undefined> {
  await ensureDataFilesExist();
  const users = await readData<User>(USERS_FILE);
  return users.find(u => u.username.toLowerCase() === username.toLowerCase());
}

export async function createUser(userData: Omit<User, 'op' | 'joinedEventIds'>): Promise<User> {
  await ensureDataFilesExist();
  const users = await readData<User>(USERS_FILE);
  if (users.some(u => u.id === userData.id || u.username.toLowerCase() === userData.username.toLowerCase())) {
    console.warn(`Attempted to create a user that already exists: ${userData.username} (ID: ${userData.id})`);
    throw new Error('用户已存在。'); 
  }
  const newUser: User = {
    ...userData,
    op: 0, // Default to non-admin
    joinedEventIds: []
  }
  users.push(newUser);
  await writeData<User>(USERS_FILE, users);
  return newUser;
}

